body {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  line-height: 21px;
  color: #ffffff;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
}

.container {
  padding: 0 15px;
  margin: 0 auto;
  min-width: 320px;
  max-width: 407px;
}
@media (min-width: 408px) {
  .container {
    padding: 0 30px;
    min-width: 408px;
    max-width: 700px;
  }
}
@media (min-width: 701px) {
  .container {
    min-width: 701px;
    max-width: 999px;
  }
}
@media (min-width: 1000px) {
  .container {
    padding: 0 60px;
    min-width: 1000px;
    max-width: 1439px;
  }
}
@media (min-width: 1440px) {
  .container {
    width: 1440px;
  }
}
@media (min-width: 1000px) {
  .container--nav {
    padding: 0 30px;
    margin: 0 auto;
  }
}
.container--blog {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr;
}
@media (min-width: 1000px) {
  .container--blog-article {
    padding: 0 250px;
  }
}

.page {
  display: grid;
  grid-template-columns: 1fr;
  min-height: 100vh;
  font-family: "Roboto", sans-serif;
  font-size: 15px;
  line-height: 18px;
  font-weight: 400;
  font-style: normal;
}
@media (min-width: 701px) {
  .page {
    font-size: 16px;
    line-height: 19px;
  }
}
@media (min-width: 1000px) {
  .page {
    font-size: 20px;
    line-height: 23px;
  }
}
.page--blog {
  grid-template-rows: auto 1fr auto;
}

.page-main--blog {
  padding-top: 100px;
}
@media (min-width: 701px) {
  .page-main--blog {
    padding-top: 110px;
  }
}
@media (min-width: 1000px) {
  .page-main--blog {
    padding-top: 130px;
  }
}

.page-header {
  padding-bottom: 30px;
  background-color: #8E0028;
}
@media (min-width: 1000px) {
  .page-header {
    padding-bottom: 0;
  }
}
.page-header--blog {
  background-color: #ffffff;
  padding-bottom: 0;
}

.page-header__intro {
  margin-bottom: 43px;
}
@media (min-width: 1000px) {
  .page-header__intro {
    margin-bottom: 0;
  }
}

.page-nav {
  position: fixed;
  z-index: 1000;
  top: -1px;
  left: 0;
  width: 100vw;
  min-height: 75px;
}

.page-nav__common-wrapper {
  margin-right: -1px;
  margin-left: -1px;
  border-bottom-right-radius: 15px;
  border-bottom-left-radius: 15px;
}

.page-nav__common-inner {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 15px;
  padding-bottom: 15px;
  border-bottom-right-radius: 15px;
  border-bottom-left-radius: 15px;
  z-index: 30;
  background-color: rgba(255, 255, 255, 0.3);
}
.page-nav__common-inner--blog {
  box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.05);
}
.page-nav__common-inner--white {
  background-color: #ffffff;
}
@media (min-width: 1000px) {
  .page-nav__common-inner {
    padding-top: 20px;
    padding-bottom: 20px;
    border-bottom-right-radius: 20px;
    border-bottom-left-radius: 20px;
  }
}

.page-nav__toggler {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 35px;
  border: none;
  padding: 0;
  justify-self: right;
  background-color: transparent;
  outline: none;
}
.page-nav__toggler:hover, .page-nav__toggler:focus {
  background-color: rgba(245, 245, 245, 0.5);
}
.page-nav__toggler--closed::before {
  content: "";
  position: absolute;
  top: 12px;
  right: 0;
  width: 24px;
  height: 2px;
  transform: rotate(0);
  border-radius: 5px;
  background-color: #ffffff;
  transition: transform 0.3s;
}
.page-nav__toggler--closed::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: 12px;
  width: 24px;
  height: 2px;
  transform: rotate(0);
  border-radius: 5px;
  background-color: #ffffff;
  transition: transform 0.3s;
}
.page-nav__toggler--opened::before {
  content: "";
  position: absolute;
  top: 16px;
  right: 0;
  transform: rotate(45deg);
  width: 24px;
  height: 2px;
  border-radius: 5px;
  background-color: #6E6E6E;
  transition: transform 0.3s;
}
.page-nav__toggler--opened::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: 17px;
  transform: rotate(-45deg);
  width: 24px;
  height: 2px;
  border-radius: 5px;
  background-color: #6E6E6E;
  transition: transform 0.3s;
}

.page-nav__content-wrapper {
  position: relative;
  z-index: 10;
  display: grid;
  grid-template-columns: 1fr;
  margin-top: -20px;
  background-color: #ffffff;
  border-bottom-right-radius: 15px;
  border-bottom-left-radius: 15px;
  box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.05);
}
@media (min-width: 701px) {
  .page-nav__content-wrapper {
    padding-top: 55px;
    padding-bottom: 50px;
    grid-template-columns: repeat(2, 50%);
    justify-content: space-between;
  }
}
@media (min-width: 1000px) {
  .page-nav__content-wrapper {
    display: block;
    z-index: 110;
    margin-top: 0;
  }
}

@media (min-width: 1000px) {
  .page-nav__buttons-wrapper {
    display: grid;
    position: absolute;
    top: -60px;
    right: 100px;
  }
}

.page-nav__buttons-inner {
  display: grid;
  grid-template-columns: 1fr;
  gap: 10px;
  margin: 0 0 35px;
}
@media (min-width: 408px) {
  .page-nav__buttons-inner {
    max-width: 348px;
    margin-right: auto;
    margin-left: auto;
  }
}
@media (min-width: 701px) {
  .page-nav__buttons-inner {
    gap: 25px;
    margin-right: 0;
    margin-bottom: 0;
  }
}
@media (min-width: 1000px) {
  .page-nav__buttons-inner {
    grid-template-columns: repeat(5, auto);
    gap: 9px;
    max-width: 241px;
    margin: 0;
  }
}

.page-nav__links-inner {
  display: none;
}

.page-nav__menu-list {
  display: grid;
  grid-template-columns: 1fr;
  gap: 10px;
  margin: 25px 0 25px;
  padding: 0;
  list-style: none;
}
@media (min-width: 408px) {
  .page-nav__menu-list {
    width: 348px;
    margin-right: auto;
    margin-left: auto;
  }
}
@media (min-width: 701px) {
  .page-nav__menu-list {
    width: 190px;
    margin: 0 auto 0 0;
    gap: 15px;
  }
}
@media (min-width: 1000px) {
  .page-nav__menu-list {
    grid-template-columns: repeat(auto-fit, minmax(197px, 1fr));
    width: 100%;
    margin-top: -20px;
    padding: 35px 30px 101px;
    background-color: #ffffff;
    border-bottom-right-radius: 20px;
    border-bottom-left-radius: 20px;
    box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.05);
  }
}

@media (min-width: 408px) {
  .page-nav__menu-item {
    text-align: center;
  }
}
@media (min-width: 701px) {
  .page-nav__menu-item {
    text-align: left;
  }
}
.page-nav__menu-item--blog {
  position: relative;
  display: flex;
  justify-content: start;
  align-items: center;
  width: 92px;
  padding: 6px 15px;
  background-color: #FF3F4A;
  border-radius: 5px;
}
.page-nav__menu-item--blog:hover, .page-nav__menu-item--blog:focus {
  background-color: #292F51;
}
@media (min-width: 408px) {
  .page-nav__menu-item--blog {
    margin: 0 auto;
  }
}
@media (min-width: 701px) {
  .page-nav__menu-item--blog {
    margin: unset;
    width: 102px;
  }
}
@media (min-width: 1000px) {
  .page-nav__menu-item--blog {
    margin: unset;
    width: 92px;
  }
}

.page-nav__menu-link {
  position: relative;
  display: inline-block;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: #6E6E6E;
  outline: none;
}
.page-nav__menu-link:hover, .page-nav__menu-link:focus {
  color: #FF3F4A;
}
@media (min-width: 701px) {
  .page-nav__menu-link {
    font-size: 22px;
    line-height: 26px;
  }
}
@media (min-width: 1000px) {
  .page-nav__menu-link {
    font-size: 18px;
    line-height: 20px;
    padding-left: 17px;
  }
  .page-nav__menu-link::before {
    content: "";
    position: absolute;
    top: 6px;
    left: 0;
    width: 8px;
    height: 8px;
    border-radius: 50%;
  }
  .page-nav__menu-link--1::before {
    background-color: #E43628;
  }
  .page-nav__menu-link--2::before {
    background-color: #ED7604;
  }
  .page-nav__menu-link--3::before {
    background-color: #369BD7;
  }
  .page-nav__menu-link--4::before {
    background-color: #0065B1;
  }
  .page-nav__menu-link--5::before {
    background-color: #008F40;
  }
  .page-nav__menu-link--6::before {
    background-color: #8F328A;
  }
  .page-nav__menu-link--7::before {
    background-color: #FFC83A;
  }
  .page-nav__menu-link--8::before {
    background-color: #13C9B3;
  }
  .page-nav__menu-link--9::before {
    background-color: #14BF00;
  }
}
.page-nav__menu-link--blog {
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: #ffffff;
}
.page-nav__menu-link--blog:hover, .page-nav__menu-link--blog:focus {
  color: #ffffff;
}
.page-nav__menu-link--blog::after {
  content: "";
  position: absolute;
  top: 3px;
  right: -20px;
  width: 15px;
  height: 13px;
  background-image: url("../../img/svg/strelka-blog.svg");
  background-repeat: no-repeat;
  background-position: center center;
}
@media (min-width: 701px) {
  .page-nav__menu-link--blog {
    font-size: 22px;
    line-height: 26px;
  }
  .page-nav__menu-link--blog::after {
    top: 7px;
    width: 16px;
    height: 14px;
  }
}
@media (min-width: 1000px) {
  .page-nav__menu-link--blog {
    font-size: 18px;
    line-height: 20px;
    padding-left: 0;
  }
  .page-nav__menu-link--blog::after {
    top: 3px;
    width: 16px;
    height: 14px;
  }
}

.page-nav--closed .page-nav__menu-list,
.page-nav--closed .page-nav__buttons-wrapper {
  display: none;
}

@media (min-width: 1000px) {
  .page-nav--closed .page-nav__buttons-wrapper {
    display: grid;
  }
}

.page-nav--opened .page-nav__menu-list {
  display: grid;
}

.page-nav--opened .page-nav__common-inner--blog {
  box-shadow: none;
}

@media (min-width: 320px) {
  .page-nav--closed .page-nav__content-wrapper {
    transform: translateY(-80%) scaleY(0);
    opacity: 0.8;
  }
}
@media (min-width: 1000px) {
  .page-nav--closed .page-nav__content-wrapper {
    opacity: 1;
    transform: unset;
    padding: 0;
    background-color: transparent;
    border-bottom-right-radius: 15px;
    border-bottom-left-radius: 15px;
    box-shadow: none;
  }
}

@media (min-width: 320px) {
  .page-nav--opened .page-nav__content-wrapper {
    transform-origin: top;
    transform: translateY(0%) scaleY(1);
    opacity: 1;
    transition: 0.4s;
  }
}
@media (min-width: 1000px) {
  .page-nav--opened .page-nav__content-wrapper {
    opacity: 1;
    transform: unset;
    padding: 0;
    background-color: transparent;
    border-bottom-right-radius: 15px;
    border-bottom-left-radius: 15px;
    box-shadow: none;
  }
}

.page-nav__common-inner--white .page-nav__toggler::before,
.page-nav__common-inner--white .page-nav__toggler::after {
  background-color: #6E6E6E;
}

@media (min-width: 1000px) {
  .page-nav--opened .page-nav__buttons-wrapper {
    top: auto;
    bottom: 30px;
  }
}

@media (min-width: 1000px) {
  .page-nav--closed .page-nav__menu-list {
    transform: translateY(-80%) scaleY(0);
    opacity: 0.8;
    transition: 0.4s;
  }
}

@media (min-width: 1000px) {
  .page-nav--opened .page-nav__menu-list {
    transform-origin: top;
    transform: translateY(0%) scaleY(1);
    opacity: 1;
  }
}

.logo {
  fill: white;
}
.logo--color #logo-svg-white {
  --color-p: #0B3D86;
  --color-a: #EE7601;
  --color-i: #0065B1;
  --color-n: #008E3D;
  --color-t: #903189;
  --color-erest: #FF3F4A;
}
@media (min-width: 1000px) {
  .logo {
    width: 200px;
    height: 37px;
  }
}

.button-nav {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 20px;
  border-radius: 5px;
  outline: none;
}
.button-nav--ozon {
  background-color: #005BFF;
}
.button-nav--wb {
  background-color: #AC0AC7;
}
.button-nav--yandex {
  color: #000000;
  background-color: #FED104;
}
.button-nav--whatsapp {
  background-color: #29A61A;
}
.button-nav--telegram {
  background-color: #039BE4;
}
@media (min-width: 701px) {
  .button-nav {
    font-size: 20px;
    line-height: 20px;
    padding: 12px 24px;
