    border-radius: 15px;
  }
}

.accordion__item {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 15px 45px 15px 15px;
  border: none;
  background-color: #ffffff;
  outline: none;
}
.accordion__item::before {
  content: "";
  position: absolute;
  top: 19px;
  right: 15px;
  width: 16px;
  height: 16px;
  transform: rotate(45deg);
  background-image: url("../../img/svg/button-accordion-mobile.svg");
  background-position: center center;
  background-repeat: no-repeat;
  transition: transform 0.3s ease-in-out;
}
.accordion__item--collapse::before {
  transform: rotate(0deg);
}
.accordion__item:hover .accordion__title, .accordion__item:focus .accordion__title {
  color: rgba(110, 110, 110, 0.5);
}
.accordion__item:hover .accordion__title::before, .accordion__item:focus .accordion__title::before {
  margin-right: 12px;
}
@media (min-width: 701px) {
  .accordion__item {
    padding: 18px 60px 18px 25px;
  }
  .accordion__item::before {
    top: 24px;
    right: 20px;
    width: 20px;
    height: 20px;
    background-size: contain;
  }
  .accordion__item:hover .accordion__title::before, .accordion__item:focus .accordion__title::before {
    margin-right: 17px;
  }
}
@media (min-width: 1000px) {
  .accordion__item {
    padding: 18px 60px 18px 30px;
  }
  .accordion__item::before {
    top: 33px;
    right: 35px;
    width: 20px;
    height: 20px;
    background-size: contain;
  }
  .accordion__item:hover .accordion__title::before, .accordion__item:focus .accordion__title::before {
    margin-right: 24px;
  }
}

.accordion__title {
  position: relative;
  display: flex;
  align-items: center;
  text-align: left;
  font-size: 15px;
  line-height: 17px;
  font-weight: 500;
  color: #6E6E6E;
  transition: color 0.1s ease-in-out;
}
.accordion__title::before {
  content: attr(data-number);
  margin-right: 7px;
  color: #ffffff;
  width: 25px;
  height: 25px;
  align-self: flex-start;
  display: flex;
  justify-content: center;
  flex-shrink: 0;
  align-items: center;
  border-radius: 50%;
  transition: margin 0.25s ease-in-out;
}
.accordion__title--1::before {
  background-color: #E43628;
}
.accordion__title--2::before {
  background-color: #ED7604;
}
.accordion__title--3::before {
  background-color: #369BD7;
}
.accordion__title--4::before {
  background-color: #0065B1;
}
.accordion__title--5::before {
  background-color: #008F40;
}
.accordion__title--6::before {
  background-color: #8F328A;
}
.accordion__title--7::before {
  background-color: #13C9B3;
}
.accordion__title--8::before {
  background-color: #FFC83A;
}
@media (min-width: 701px) {
  .accordion__title {
    font-size: 19px;
    line-height: 21px;
    font-weight: 400;
    color: #6E6E6E;
  }
  .accordion__title::before {
    margin-right: 12px;
    width: 35px;
    height: 35px;
  }
}
@media (min-width: 1000px) {
  .accordion__title {
    font-size: 22px;
    line-height: 22px;
    font-weight: 500;
  }
